  height: 0 !important;
  width: 0 !important;
  position: absolute;
  z-index: 1;
  margin-top: 23px !important;
  margin-left: 5px !important;
  pointer-events: none;
  opacity: 0; }

.form-controls__dropdown {
  box-sizing: border-box;
  display: inline-block;
  vertical-align: top;
  position: relative; }
  .form-controls__dropdown-open .form-controls__dropdown-list {
    display: block; }
  .form-controls__dropdown-button {
    background: #FFF;
    border: 2px solid #ed1c35;
    border-radius: 3px;
    min-width: 120px;
    padding: 0;
    position: relative; }
    .form-controls__dropdown-button__container {
      display: table;
      width: 100%;
      height: 36px; }
  .form-controls__dropdown-text, .form-controls__dropdown-tick {
    display: table-cell;
    height: inherit; }
  .form-controls__dropdown-text {
    line-height: 36px;
    padding: 0 1rem;
    text-align: left; }
  .form-controls__dropdown-placeholder {
    color: #999; }
  .form-controls__dropdown-tick {
